import {CheckCircleIcon, KebabHorizontalIcon, PencilIcon, TrashIcon} from '@primer/octicons-react'
import type {SxProp} from '@primer/react'
import {ActionList, ActionMenu, IconButton} from '@primer/react'

export interface SectionActionMenuProps {
  anchorSx?: SxProp['sx']
  buttonAriaLabel: string
  canDelete?: boolean
  isSectionDirty?: boolean
  onDelete?: () => void
  onEditTitle?: () => void
  onSaveChanges?: () => void
}

export default function SectionActionMenu({
  anchorSx,
  buttonAriaLabel,
  canDelete,
  isSectionDirty,
  onEditTitle,
  onDelete,
  onSaveChanges,
}: SectionActionMenuProps) {
  return (
    <ActionMenu>
      <ActionMenu.Anchor>
        {/* eslint-disable-next-line primer-react/a11y-remove-disable-tooltip */}
        <IconButton
          unsafeDisableTooltip={true}
          aria-label={buttonAriaLabel}
          icon={KebabHorizontalIcon}
          sx={{
            ...anchorSx,
            ...(isSectionDirty
              ? {
                  '&::before': {
                    content: '""',
                    position: 'absolute',
                    right: 0,
                    top: 0,
                    width: 8,
                    height: 8,
                    border: '2px solid',
                    borderRadius: 100,
                    borderColor: 'canvas.subtle',
                    backgroundColor: 'attention.fg',
                    transform: 'translate(4px, -4px)',
                  },
                  position: 'relative',
                }
              : {}),
          }}
          variant="invisible"
        />
      </ActionMenu.Anchor>

      <ActionMenu.Overlay>
        <ActionList>
          <ActionList.Item onSelect={onEditTitle}>
            <ActionList.LeadingVisual>
              <PencilIcon />
            </ActionList.LeadingVisual>
            Rename
          </ActionList.Item>
          <ActionList.Item disabled={!isSectionDirty} onSelect={onSaveChanges}>
            <ActionList.LeadingVisual>
              <CheckCircleIcon />
            </ActionList.LeadingVisual>
            Save changes
          </ActionList.Item>
          <ActionList.Item disabled={!canDelete} onSelect={onDelete}>
            <ActionList.LeadingVisual>
              <TrashIcon />
            </ActionList.LeadingVisual>
            Delete
          </ActionList.Item>
        </ActionList>
      </ActionMenu.Overlay>
    </ActionMenu>
  )
}

try{ SectionActionMenu.displayName ||= 'SectionActionMenu' } catch {}