import {ScrollToTop} from '@github-ui/issue-viewer/ScrollToTop'
import {ListView} from '@github-ui/list-view'
import {ListViewDensityToggle} from '@github-ui/list-view/ListViewDensityToggle'
import {ListViewMetadata} from '@github-ui/list-view/ListViewMetadata'
import {IssueRow} from '@github-ui/list-view-items-issues-prs/IssueRow'
import {NoResults} from '@github-ui/list-view-items-issues-prs/NoResults'
import {PaginationLoading} from '@github-ui/list-view-items-issues-prs/PaginationLoading'
import {PullRequestRow} from '@github-ui/list-view-items-issues-prs/PullRequestRow'
import type {QUERY_FIELDS} from '@github-ui/list-view-items-issues-prs/Queries'
import {replaceInQuery} from '@github-ui/list-view-items-issues-prs/Query'
import {SortingOptionsMenu} from '@github-ui/list-view-items-issues-prs/SortingOptionsMenu'
import type {AnalyticsEvent} from '@github-ui/use-analytics'
import {Box, Spinner} from '@primer/react'
import {useCallback, useMemo, useState} from 'react'
import useInfiniteScroll from 'react-infinite-scroll-hook'
import type {PreloadedQuery} from 'react-relay'
import {graphql, usePaginationFragment, usePreloadedQuery} from 'react-relay'
import type {To} from 'react-router-dom'

import {LABELS, TEST_IDS, VALUES} from '../../constants'
import {useQueryContext} from '../../contexts'
import {usePullsAnalytics} from '../../hooks/use-pulls-analytics'
import {searchUrl} from '../../utils/urls'
import type {ListItems_query$key} from './__generated__/ListItems_query.graphql'
import type {SearchQuery} from './__generated__/SearchQuery.graphql'
import {searchRoot} from './Search'

type ListProps = {
  isDetailPage?: boolean
  queryRef: PreloadedQuery<SearchQuery>
}

function useSearchResults(key: ListItems_query$key) {
  const {data, loadNext, hasNext, isLoadingNext} = usePaginationFragment<SearchQuery, ListItems_query$key>(
    graphql`
      fragment ListItems_query on Query
      @argumentDefinitions(
        cursor: {type: "String", defaultValue: null}
        query: {type: "String!"}
        first: {type: "Int"}
        labelPageSize: {type: "Int!"}
      )
      @refetchable(queryName: "RefetchableSearchQuery") {
        search(first: $first, after: $cursor, query: $query, type: ISSUE) @connection(key: "Query_search") {
          edges {
            node {
              ... on Issue {
                __id
                __typename
                number
                # eslint-disable-next-line relay/must-colocate-fragment-spreads
                ...IssueRow @arguments(labelPageSize: $labelPageSize, fetchRepository: true)
              }
              ... on PullRequest {
                __id
                __typename
                number
                # eslint-disable-next-line relay/must-colocate-fragment-spreads
                ...PullRequestRow_pullRequest @arguments(labelPageSize: $labelPageSize)
              }
            }
          }
          pageInfo {
            startCursor
            endCursor
            hasPreviousPage
            hasNextPage
          }
          issueCount
        }
      }
    `,
    key,
  )

  return {data, loadNext, hasNext, isLoadingNext}
}

/**
 * See ListView stories for a representation of this component.
 * ui/packages/list-view/src/stories/PullRequestDashboard/PullRequestDashboard.stories.tsx
 * https://ui.githubapp.com/storybook/?path=/story/recipes-list-view-dotcom-pages--pull-request-dashboard
 */
export function ListItems({isDetailPage = false, queryRef}: ListProps) {
  const preloadedData = usePreloadedQuery<SearchQuery>(searchRoot, queryRef)
  const {data: pageData, loadNext, hasNext: hasNextPage, isLoadingNext} = useSearchResults(preloadedData)
  const [reactionEmojiToDisplay, setReactionEmojiToDisplay] = useState({reaction: '', reactionEmoji: ''})
  const [sortingItemSelected, setSortingItemSelected] = useState<string>('')
  const {
    activeSearchQuery,
    dirtySearchQuery,
    executeQuery,
    setActiveSearchQuery,
    setDirtySearchQuery,
    setIsQueryLoading,
    isQueryLoading,
  } = useQueryContext()
  const {sendPullsAnalyticsEvent} = usePullsAnalytics()

  const getMetadataHref = (queryField: keyof typeof QUERY_FIELDS, metadataName: string) => {
    return searchUrl(replaceInQuery(activeSearchQuery, queryField, metadataName))
  }

  const onSelectRow = useCallback(
    (payload?: {[key: string]: unknown} | AnalyticsEvent | undefined) => {
      sendPullsAnalyticsEvent('search_results.select_row', 'SEARCH_RESULT_ROW', {...payload})
    },
    [sendPullsAnalyticsEvent],
  )

  const onIssueNavigate = useCallback((to: To) => {
    window.open(`${window.location.origin}${to}`)
  }, [])

  const data = useMemo(
    () =>
      pageData.search.edges
        ? pageData.search.edges
            .map(edge =>
              edge?.node && (edge.node.__typename === 'PullRequest' || edge.node.__typename === 'Issue')
                ? edge.node
                : null,
            )
            .filter(node => node != null)
        : [],
    [pageData.search.edges],
  )

  const [sentryRef] = useInfiniteScroll({
    loading: isLoadingNext,
    hasNextPage,
    onLoadMore: () => loadNext(VALUES.issuesPageSize),
    // `rootMargin` is passed to `IntersectionObserver`.
    // We can use it to trigger 'onLoadMore' when the sentry comes near to become
    // visible, instead of becoming fully visible on the screen.
    rootMargin: '0px 0px 400px 0px',
  })

  const listViewMetadata = (
    <ListViewMetadata
      title={LABELS.numberOfResults(pageData.search.issueCount)}
      densityToggle={<ListViewDensityToggle />}
      assistiveAnnouncement={isQueryLoading ? LABELS.loadingQueryResults : undefined}
    >
      {isQueryLoading && <Spinner size="small" sx={{marginLeft: 2, marginRight: 'auto'}} />}
      <SortingOptionsMenu
        activeSearchQuery={activeSearchQuery}
        dirtySearchQuery={dirtySearchQuery}
        executeQuery={executeQuery}
        setActiveSearchQuery={setActiveSearchQuery}
        setDirtySearchQuery={setDirtySearchQuery}
        setIsQueryLoading={setIsQueryLoading}
        setReactionEmojiToDisplay={setReactionEmojiToDisplay}
        setSortingItemSelected={setSortingItemSelected}
        sortingItemSelected={sortingItemSelected}
        searchUrl={searchUrl}
      />
    </ListViewMetadata>
  )

  return (
    <>
      <Box sx={{border: '1px solid', borderColor: 'border.muted', borderRadius: 2}}>
        {data?.length === 0 ? (
          <>
            {/* {listViewMetadata} */}
            <NoResults />
          </>
        ) : (
          <div>
            <ScrollToTop startY={96} />
            <ListView
              metadata={listViewMetadata}
              data-testid={TEST_IDS.list}
              title={LABELS.searchResults}
              titleHeaderTag="h2"
            >
              {data?.map(node => {
                const sharedRowData = {
                  key: node?.__id,
                  isDetailPage,
                  isActive: false,
                  'data-testid': TEST_IDS.issueRowItem(node?.number || '-1'),
                  getMetadataHref,
                  onSelectRow,
                  reactionEmojiToDisplay,
                  sortingItemSelected,
                  showRepository: true,
                }
                if (node == null) {
                  return null
                }

                if (node.__typename === 'Issue') {
                  return (
                    <IssueRow
                      issueKey={node}
                      metadataRef={undefined}
                      {...sharedRowData}
                      key={sharedRowData.key}
                      onNavigate={onIssueNavigate}
                    />
                  )
                }

                if (node.__typename === 'PullRequest') {
                  return (
                    <PullRequestRow
                      pullRequestKey={node}
                      {...sharedRowData}
                      key={sharedRowData.key}
                      showAvatar={activeSearchQuery.indexOf('is:pr') > -1}
                    />
                  )
                }
              })}
              {isLoadingNext && <PaginationLoading />}
              {hasNextPage && <div aria-hidden="true" ref={sentryRef} />}
            </ListView>
          </div>
        )}
      </Box>
    </>
  )
}

try{ ListItems.displayName ||= 'ListItems' } catch {}