import {commitMutation, graphql} from 'react-relay'
import type {Environment} from 'relay-runtime'

import type {
  UpdateSavedViewInput,
  updateSavedViewMutation,
  updateSavedViewMutation$data,
} from './__generated__/updateSavedViewMutation.graphql'

type UpdateSavedViewMutationProps = {
  environment: Environment
  input: UpdateSavedViewInput
  onError?: (error: Error) => void
  onCompleted?: (response: updateSavedViewMutation$data) => void
}

export function updateSavedViewMutation({environment, input, onError, onCompleted}: UpdateSavedViewMutationProps) {
  return commitMutation<updateSavedViewMutation>(environment, {
    mutation: graphql`
      mutation updateSavedViewMutation($input: UpdateSavedViewInput!) @raw_response_type {
        updateSavedView(input: $input) {
          savedView {
            id
            query
            name
          }
        }
      }
    `,
    variables: {
      input,
    },
    onError: error => onError?.(error),
    onCompleted: (response: updateSavedViewMutation$data) => onCompleted?.(response),
  })
}
