import type {PropsWithChildren} from 'react'
import {createContext, memo, useContext} from 'react'
import type {PreloadedQuery, UseQueryLoaderLoadQueryOptions} from 'react-relay'
import {useQueryLoader} from 'react-relay'

import type {SearchQuery, SearchQuery$variables} from '../components/search/__generated__/SearchQuery.graphql'
import {searchRoot} from '../components/search/Search'

const Empty = Symbol('empty')
const SearchQueryRefContext = createContext<
  PreloadedQuery<SearchQuery, Record<string, unknown>> | null | undefined | typeof Empty
>(Empty)
const LoadSearchQueryContext = createContext<
  ((variables: SearchQuery$variables, options?: UseQueryLoaderLoadQueryOptions | undefined) => void) | typeof Empty
>(Empty)

export const SearchQueryRefProvider = memo(function SearchQueryRefProvider({children}: PropsWithChildren) {
  const [searchQueryRef, loadSearchQuery] = useQueryLoader<SearchQuery>(searchRoot)
  return (
    <SearchQueryRefContext.Provider value={searchQueryRef}>
      <LoadSearchQueryContext.Provider value={loadSearchQuery}>{children}</LoadSearchQueryContext.Provider>
    </SearchQueryRefContext.Provider>
  )
})

export function useSearchQueryRef() {
  const searchQueryRef = useContext(SearchQueryRefContext)
  if (searchQueryRef === Empty) throw new Error('useSearchQueryRef must be called inside a SearchQueryRefContext')
  return searchQueryRef
}
export function useLoadSearchQuery() {
  const loadSearchQuery = useContext(LoadSearchQueryContext)
  if (loadSearchQuery === Empty) throw new Error('useLoadSearchQueryRef must be called inside a LoadSearchQueryContext')
  return loadSearchQuery
}

try{ Empty.displayName ||= 'Empty' } catch {}
try{ SearchQueryRefProvider.displayName ||= 'SearchQueryRefProvider' } catch {}