import {ListLoading} from '@github-ui/list-view-items-issues-prs/ListLoading'
import {getQuery} from '@github-ui/list-view-items-issues-prs/Query'
import PreloadedQueryBoundary from '@github-ui/relay-preloaded-query-boundary'
import {Suspense} from 'react'
import type {PreloadedQuery, UseQueryLoaderLoadQueryOptions} from 'react-relay'
import {useLocation} from 'react-router-dom'

import {LABELS, VALUES} from '../../constants'
import type {SearchQuery, SearchQuery$variables} from './__generated__/SearchQuery.graphql'
import {ListError} from './ListError'
import {ListItems} from './ListItems'

type SearchList = {
  isDetailPage: boolean
  query: string
  searchQueryRef: PreloadedQuery<SearchQuery>
  loadSearchQuery: (vars: SearchQuery$variables, opts: UseQueryLoaderLoadQueryOptions) => void
}

export function SearchList({isDetailPage, query, searchQueryRef, loadSearchQuery}: SearchList) {
  const {pathname} = useLocation()
  const showBorder = !(isDetailPage || pathname === '/issues/new')
  if (!searchQueryRef) return null

  return (
    <Suspense
      fallback={
        <ListLoading
          headerTitle={LABELS.loadingListingResults}
          showBorder={showBorder}
          layoutDensity={'default'}
          pageSize={10}
        />
      }
    >
      <PreloadedQueryBoundary
        fallback={ListError}
        onRetry={() => {
          if (!query) return
          loadSearchQuery(
            {
              query: getQuery(query),
              first: VALUES.issuesPageSize,
              labelPageSize: VALUES.labelPageSize,
            },
            {fetchPolicy: 'network-only'},
          )
        }}
      >
        <ListItems isDetailPage={isDetailPage} queryRef={searchQueryRef} />
      </PreloadedQueryBoundary>
    </Suspense>
  )
}

try{ SearchList.displayName ||= 'SearchList' } catch {}