import {ActionListLoading} from '@github-ui/list-view-items-issues-prs/ActionListLoading'
import {Box} from '@primer/react'

import {TEST_IDS, VALUES} from '../../constants'
import type {SectionHeaderProps} from './SectionHeader'
import SectionHeader from './SectionHeader'

interface SectionLoadingProps extends SectionHeaderProps {
  numberOfRows?: number
}

export const SectionLoading = ({numberOfRows, ...headerProps}: SectionLoadingProps) => {
  return (
    <>
      <Box
        data-testid={TEST_IDS.section(headerProps.title)}
        sx={{border: '1px solid', borderColor: 'border.muted', borderRadius: 2}}
      >
        <SectionHeader {...headerProps} />
        <ActionListLoading numberOfRows={numberOfRows ?? VALUES.issuesPageSize} />
      </Box>
    </>
  )
}

try{ SectionLoading.displayName ||= 'SectionLoading' } catch {}