import type {AnalyticsEvent} from '@github-ui/use-analytics'
import {useAnalytics} from '@github-ui/use-analytics'
import {useCallback} from 'react'

import type {PullsEventType, PullsTargetType} from '../types/analytics-event-types'

type SendPullsEventFunction = (
  eventType: PullsEventType,
  target: PullsTargetType,
  payload?: {[key: string]: unknown} | AnalyticsEvent,
) => void

/**
 * Hook used to send analytics events for Pulls actions
 */
export function usePullsAnalytics(): {
  sendPullsAnalyticsEvent: SendPullsEventFunction
} {
  const {sendAnalyticsEvent} = useAnalytics()
  return {
    sendPullsAnalyticsEvent: useCallback(
      (eventType, target, payload = {}) => {
        sendAnalyticsEvent(eventType, target, payload)
      },
      [sendAnalyticsEvent],
    ),
  }
}
