import {graphql, useFragment} from 'react-relay'

import type {SavedView_savedView$key} from './__generated__/SavedView_savedView.graphql'
import type {SectionProps} from './Section'
import Section from './Section'

interface SavedViewProps extends Pick<SectionProps, 'canDelete'> {
  savedView: SavedView_savedView$key
}

export default function SavedView({savedView, ...sectionProps}: SavedViewProps) {
  const savedViewData = useFragment(
    graphql`
      fragment SavedView_savedView on SavedView {
        id
        name
        query
      }
    `,
    savedView,
  )

  return (
    <Section
      name={savedViewData.name}
      initialSearchQuery={savedViewData.query}
      savedViewId={savedViewData.id}
      {...sectionProps}
    />
  )
}

try{ SavedView.displayName ||= 'SavedView' } catch {}