import {ListView} from '@github-ui/list-view'
import {IssueRow} from '@github-ui/list-view-items-issues-prs/IssueRow'
import {NoResults} from '@github-ui/list-view-items-issues-prs/NoResults'
import {PullRequestRow} from '@github-ui/list-view-items-issues-prs/PullRequestRow'
import type {QUERY_FIELDS} from '@github-ui/list-view-items-issues-prs/Queries'
import {getQuery, replaceInQuery} from '@github-ui/list-view-items-issues-prs/Query'
import {useAppPayload} from '@github-ui/react-core/use-app-payload'
import type {AnalyticsEvent} from '@github-ui/use-analytics'
import {Box} from '@primer/react'
import {useCallback, useEffect, useState, useTransition} from 'react'
import type {PreloadedQuery} from 'react-relay'
import {graphql, usePaginationFragment, usePreloadedQuery, useQueryLoader} from 'react-relay'
import type {To} from 'react-router-dom'

import {TEST_IDS, VALUES} from '../../constants'
import {useQueryContext} from '../../contexts'
import {usePullsAnalytics} from '../../hooks/use-pulls-analytics'
import type {AppPayload} from '../../types/app-payload'
import {searchUrl} from '../../utils/urls'
import type {Section_query$key} from './__generated__/Section_query.graphql'
import type {SectionQuery} from './__generated__/SectionQuery.graphql'
import type {SectionHeaderProps} from './SectionHeader'
import SectionHeader from './SectionHeader'
import {SectionLoading} from './SectionLoading'

function useSearchData(key: Section_query$key) {
  // read out the data we queried and get a function to load more data as the user requests it (todo)
  return usePaginationFragment(
    graphql`
      fragment Section_query on Query
      @argumentDefinitions(
        cursor: {type: "String", defaultValue: null}
        query: {type: "String!"}
        first: {type: "Int"}
        labelPageSize: {type: "Int!"}
      )
      @refetchable(queryName: "SectionRefetchQuery") {
        search(first: $first, after: $cursor, query: $query, type: ISSUE) @connection(key: "section_search") {
          edges {
            node {
              ... on Issue {
                __typename
                number
                ...IssueRow @arguments(labelPageSize: $labelPageSize, fetchRepository: true)
              }
              ... on PullRequest {
                __typename
                number
                ...PullRequestRow_pullRequest @arguments(labelPageSize: $labelPageSize)
              }
            }
          }
          pageInfo {
            startCursor
            endCursor
            hasPreviousPage
            hasNextPage
          }
          issueCount
        }
      }
    `,
    key,
  )
}

interface InternalSectionProps
  extends Pick<
    SectionHeaderProps,
    | 'activeSearchQuery'
    | 'canDelete'
    | 'initialSearchQuery'
    | 'isOperationRunning'
    | 'onOperationFinish'
    | 'onOperationStart'
    | 'savedViewId'
  > {
  name: string
  onSearch: (query: string) => void
  sectionQueryRef: PreloadedQuery<SectionQuery>
}

function SectionInternal({name, onSearch, sectionQueryRef, ...headerProps}: InternalSectionProps) {
  const preloadedData = usePreloadedQuery<SectionQuery>(sectionQuery, sectionQueryRef)
  const {data} = useSearchData(preloadedData)
  const {activeSearchQuery} = useQueryContext()
  const {sendPullsAnalyticsEvent} = usePullsAnalytics()

  const onIssueNavigate = useCallback((to: To) => {
    window.open(`${window.location.origin}${to}`)
  }, [])

  const getMetadataHref = useCallback(
    (queryField: keyof typeof QUERY_FIELDS, metadataName: string) => {
      return searchUrl(replaceInQuery(activeSearchQuery, queryField, metadataName))
    },
    [activeSearchQuery],
  )

  const onSelectRow = useCallback(
    (payload?: {[key: string]: unknown} | AnalyticsEvent | undefined) => {
      sendPullsAnalyticsEvent('search_results.select_row', 'SEARCH_RESULT_ROW', {...payload})
    },
    [sendPullsAnalyticsEvent],
  )

  const filteredData =
    data.search.edges
      ?.map(edge =>
        edge?.node && (edge.node.__typename === 'PullRequest' || edge.node.__typename === 'Issue') ? edge.node : null,
      )
      .filter(node => node != null) ?? []

  const sectionHeader = (
    <SectionHeader count={data.search.issueCount} title={name} onSearch={onSearch} {...headerProps} />
  )

  return (
    <Box
      data-testid={TEST_IDS.section(name)}
      sx={{
        border: '1px solid',
        borderColor: 'border.muted',
        borderRadius: 2,
        display: 'flex',
        flexDirection: 'column',
      }}
    >
      {filteredData.length > 0 ? (
        <ListView metadata={sectionHeader} title={name} titleHeaderTag="h2" data-testid={`list-group-${name}`}>
          {filteredData?.map((node, rowIndex) => {
            const sharedRowData = {
              key: rowIndex,
              isDetailPage: false,
              isActive: false,
              'data-testid': TEST_IDS.issueRowItem(node?.number || '-1'),
              getMetadataHref,
              onSelectRow,
              reactionEmojiToDisplay: {reaction: '', reactionEmoji: ''},
              sortingItemSelected: '',
              showRepository: true,
            }

            if (node == null) {
              return null
            }

            if (node.__typename === 'Issue') {
              return (
                <IssueRow issueKey={node} {...sharedRowData} key={sharedRowData.key} onNavigate={onIssueNavigate} />
              )
            }

            if (node.__typename === 'PullRequest') {
              return (
                <PullRequestRow
                  pullRequestKey={node}
                  {...sharedRowData}
                  key={sharedRowData.key}
                  showAvatar={activeSearchQuery.indexOf('is:pr') > -1}
                />
              )
            }
          })}
        </ListView>
      ) : (
        <>
          {sectionHeader}
          <NoResults ariaLabel={`${name} - No Results`} />
        </>
      )}
    </Box>
  )
}

const sectionQuery = graphql`
  query SectionQuery($query: String!, $first: Int, $labelPageSize: Int!) {
    ...Section_query @arguments(query: $query, first: $first, labelPageSize: $labelPageSize)
  }
`

export type SectionProps = Omit<InternalSectionProps, 'activeSearchQuery' | 'sectionQueryRef' | 'onSearch'>

/**
 * Section represents a single, editable, re-runnable query and its results. A collection (doesn't exist yet) will be
 * made up of sections.
 */
export default function Section(props: SectionProps) {
  const [activeSearchQuery, setActiveSearchQuery] = useState(props.initialSearchQuery)
  // track operation state, this could be a deletion, edit, or the query is being re-run
  const [isOperationRunning, setIsOperationRunning] = useState(false)
  const [, startTransition] = useTransition()
  const {scoped_repository: scopedRepository} = useAppPayload<AppPayload>()

  // get a reference to the query results and a function that loads the query from the server
  const [sectionQueryRef, loadSectionQuery] = useQueryLoader<SectionQuery>(sectionQuery)
  const loadSearchResults = useCallback(
    (currentQuery: string) => {
      setActiveSearchQuery(currentQuery)
      setIsOperationRunning(true)
      startTransition(() => {
        const actualQuery = getQuery(currentQuery, scopedRepository)
        loadSectionQuery(
          {
            query: actualQuery,
            first: VALUES.issuesPageSize,
            labelPageSize: VALUES.labelPageSize,
          },
          {fetchPolicy: 'store-or-network'},
        )
        setIsOperationRunning(false)
      })
    },
    [loadSectionQuery, scopedRepository],
  )

  // fetch only if query has changed
  // if the query has changed loadSearchResults will be called first and then this effect
  // relay does not do the query again because it knows that is currently in flight
  useEffect(() => {
    loadSearchResults(activeSearchQuery)
  }, [activeSearchQuery, loadSearchResults])

  return sectionQueryRef ? (
    <SectionInternal
      isOperationRunning={isOperationRunning}
      sectionQueryRef={sectionQueryRef}
      activeSearchQuery={activeSearchQuery}
      onOperationFinish={() => setIsOperationRunning(false)}
      onOperationStart={() => setIsOperationRunning(true)}
      onSearch={loadSearchResults}
      {...props}
    />
  ) : (
    <SectionLoading
      activeSearchQuery={activeSearchQuery}
      initialSearchQuery={props.initialSearchQuery}
      numberOfRows={2}
      savedViewId={props.savedViewId}
      title={props.name}
    />
  )
}

try{ SectionInternal.displayName ||= 'SectionInternal' } catch {}
try{ Section.displayName ||= 'Section' } catch {}