import {AnalyticsProvider} from '@github-ui/analytics-provider'
import {memo, useMemo} from 'react'

import {useQueryContext} from '../contexts'
import {useRouteInfo} from '../hooks'

export const AnalyticsWrapper = memo(function AnalyticsWrapper({children}: {children: React.ReactNode}) {
  const {activeSearchQuery} = useQueryContext()
  const {viewId} = useRouteInfo()
  const analyticsMetadata = useMemo(
    () => ({viewQuery: activeSearchQuery, viewId: viewId || ''}),
    [activeSearchQuery, viewId],
  )
  return (
    <AnalyticsProvider appName="pulls-dashboard" category="search" metadata={analyticsMetadata}>
      {children}
    </AnalyticsProvider>
  )
})

try{ AnalyticsWrapper.displayName ||= 'AnalyticsWrapper' } catch {}