import {setTitle} from '@github-ui/document-metadata'
import {useNavigate, useSearchParams} from '@github-ui/use-navigate'
import {Box, Heading, PageLayout} from '@primer/react'
import {UnderlineNav} from '@primer/react/deprecated'
import {type PropsWithChildren, useEffect} from 'react'
import {NavLink} from 'react-router-dom'

import {VALUES} from '../constants'
import {searchUrl} from '../utils/urls'
import {List} from './List'
import {Reviews} from './Reviews'

const defaultExploreQuery = 'is:pr state:open archived:false author:@me'
const defaultExploreUrl = searchUrl(defaultExploreQuery)

export function PullRequestsPageLayout({children}: PropsWithChildren) {
  const [searchParams] = useSearchParams()
  const exploreTabActive = searchParams.get(VALUES.searchUrlParameterName)

  useEffect(() => {
    setTitle(`Pull Requests  ${exploreTabActive ? 'Explore' : 'Reviews'}`)
  }, [exploreTabActive])

  return (
    <PageLayout containerWidth="large">
      <PageLayout.Content as="div">
        <Box
          sx={{
            display: 'flex',
            flexDirection: 'column',
            gap: 3,
          }}
        >
          <Heading as="h1" sx={{fontSize: 3}}>
            Pull requests
          </Heading>
          <UnderlineNav aria-label="Main">
            <UnderlineNav.Link as={NavLink} to="/pulls" selected={!exploreTabActive}>
              Reviews
            </UnderlineNav.Link>
            <UnderlineNav.Link as={NavLink} to={defaultExploreUrl} selected={!!exploreTabActive}>
              Explore
            </UnderlineNav.Link>
          </UnderlineNav>
          {children}
        </Box>
      </PageLayout.Content>
    </PageLayout>
  )
}

const overrideParams = new URLSearchParams({
  [VALUES.searchUrlParameterName]: 'is:pr state:open archived:false assignee:@me',
}).toString()

export function AssignedRedirect() {
  const navigate = useNavigate()

  useEffect(() => {
    navigate(
      {
        pathname: '/pulls',
        search: overrideParams,
      },
      {replace: true},
    )
  }, [navigate])

  return null
}

export function PullRequestsPageContent() {
  const [searchParams] = useSearchParams()
  const exploreTabActive = searchParams.get(VALUES.searchUrlParameterName)
  return exploreTabActive ? <List /> : <Reviews />
}

try{ PullRequestsPageLayout.displayName ||= 'PullRequestsPageLayout' } catch {}
try{ AssignedRedirect.displayName ||= 'AssignedRedirect' } catch {}
try{ PullRequestsPageContent.displayName ||= 'PullRequestsPageContent' } catch {}