import {Box} from '@primer/react'

import {Search} from '../components/search/Search'
import {useRouteInfo} from '../hooks'

export const List = () => {
  const {itemIdentifier} = useRouteInfo()
  const isDetailPage = itemIdentifier?.number !== undefined

  return (
    // eslint-disable-next-line github/a11y-role-supports-aria-props
    <Box
      sx={{
        display: 'flex',
        flexDirection: 'column',
        gap: 2,
      }}
      aria-labelledby="view-title"
    >
      <Search isDetailPage={isDetailPage} />
    </Box>
  )
}

try{ List.displayName ||= 'List' } catch {}