import {getQuery} from '@github-ui/list-view-items-issues-prs/Query'
import {useAppPayload} from '@github-ui/react-core/use-app-payload'
import {Box} from '@primer/react'
import {useCallback, useEffect} from 'react'
import type {PreloadFetchPolicy} from 'react-relay'
import {graphql} from 'react-relay'

import {VALUES} from '../../constants'
import {useQueryContext} from '../../contexts'
import {useLoadSearchQuery, useSearchQueryRef} from '../../contexts/SearchQueryRefContext'
import {usePullsAnalytics} from '../../hooks/use-pulls-analytics'
import type {AppPayload} from '../../types/app-payload'
import {SearchBar} from './SearchBar'
import {SearchList} from './SearchList'

type SearchProps = {
  isDetailPage: boolean
}

export const searchRoot = graphql`
  query SearchQuery($query: String!, $first: Int, $labelPageSize: Int!) {
    # eslint-disable-next-line relay/must-colocate-fragment-spreads TODO: fix this
    ...ListItems_query @arguments(query: $query, first: $first, labelPageSize: $labelPageSize)
  }
`

export function Search({isDetailPage}: SearchProps) {
  const {activeSearchQuery, setActiveSearchQuery, setExecuteQuery, setIsQueryLoading} = useQueryContext()
  const {sendPullsAnalyticsEvent} = usePullsAnalytics()
  const {scoped_repository} = useAppPayload<AppPayload>()
  const loadSearchQuery = useLoadSearchQuery()
  const searchQueryRef = useSearchQueryRef()

  const loadSearchResults = useCallback(
    (currentQuery: string, fetchPolicy: PreloadFetchPolicy = 'store-or-network') => {
      loadSearchQuery(
        {
          query: getQuery(currentQuery, scoped_repository),
          first: VALUES.issuesPageSize,
          labelPageSize: VALUES.labelPageSize,
        },
        {fetchPolicy},
      )
    },

    [loadSearchQuery, scoped_repository],
  )

  // fetch only if query has changed
  // if the query has changed executeSearch will be called first and then this effect
  // relay does not do the query again because it knows that is currently in flight
  useEffect(() => {
    activeSearchQuery && loadSearchResults(activeSearchQuery)
    return () => {
      setIsQueryLoading(false)
    }
  }, [activeSearchQuery, loadSearchResults, setIsQueryLoading])

  const executeSearch = useCallback(
    (newQuery: string) => {
      setActiveSearchQuery(newQuery)
      loadSearchResults(newQuery, 'network-only')
      setIsQueryLoading(false)
      sendPullsAnalyticsEvent('search.execute', 'FILTER_BAR_INPUT', {new_query: newQuery})
    },
    [loadSearchResults, sendPullsAnalyticsEvent, setActiveSearchQuery, setIsQueryLoading],
  )

  useEffect(() => {
    setExecuteQuery(() => executeSearch)
  }, [executeSearch, setExecuteQuery])

  return (
    <Box sx={{display: 'flex', flexDirection: 'column', gap: 3}}>
      <SearchBar />
      {searchQueryRef && (
        <SearchList
          isDetailPage={isDetailPage}
          searchQueryRef={searchQueryRef}
          loadSearchQuery={loadSearchQuery}
          query={activeSearchQuery}
        />
      )}
    </Box>
  )
}

try{ Search.displayName ||= 'Search' } catch {}