import {commitMutation, graphql} from 'react-relay'
import type {Environment} from 'relay-runtime'

import type {
  DeleteSavedViewInput,
  deleteSavedViewMutation,
  deleteSavedViewMutation$data,
} from './__generated__/deleteSavedViewMutation.graphql'

type DeleteSavedViewMutationProps = {
  environment: Environment
  input: DeleteSavedViewInput
  onError?: (error: Error) => void
  onCompleted?: (response: deleteSavedViewMutation$data) => void
}

export function deleteSavedViewMutation({environment, input, onError, onCompleted}: DeleteSavedViewMutationProps) {
  return commitMutation<deleteSavedViewMutation>(environment, {
    mutation: graphql`
      mutation deleteSavedViewMutation($input: DeleteSavedViewInput!) @raw_response_type {
        deleteSavedView(input: $input) {
          savedView {
            id @deleteRecord
          }
        }
      }
    `,
    variables: {
      input,
    },
    onError: error => onError?.(error),
    onCompleted: (response: deleteSavedViewMutation$data) => onCompleted?.(response),
  })
}
