import {useIsWithinListView} from '@github-ui/list-view/ListViewIdContext'
import {ListViewMetadata} from '@github-ui/list-view/ListViewMetadata'
import {ListViewSectionFilterLink} from '@github-ui/list-view/ListViewSectionFilterLink'
// eslint-disable-next-line no-restricted-imports
import {useToastContext} from '@github-ui/toast/ToastContext'
import {useNavigate} from '@github-ui/use-navigate'
import {useUser} from '@github-ui/use-user'
import {SearchIcon} from '@primer/octicons-react'
import {Box, Button, IconButton, Spinner, TextInput} from '@primer/react'
import {useCallback, useEffect, useMemo, useRef, useState} from 'react'
import {useRelayEnvironment} from 'react-relay'

import {LABELS} from '../../constants'
import {deleteSavedViewMutation} from '../../mutations/delete-saved-view-mutation'
import {updateSavedViewMutation} from '../../mutations/update-saved-view-mutation'
import {searchUrl} from '../../utils/urls'
import SearchInput from '../SearchInput'
import DeleteSectionDialog from './DeleteSectionDialog'
import SectionActionMenu from './SectionActionMenu'

interface SectionSearchInputProps {
  isOpen?: boolean
  isDirty?: boolean
  onClose: () => void
  onOpen: () => void
  onSearch?: (query: string) => void
  onSetSearchQuery: (query: string) => void
  searchQuery: string
  sectionName: string
}

function SectionSearchInput({
  isOpen,
  isDirty,
  onClose,
  onOpen,
  onSearch,
  onSetSearchQuery,
  searchQuery,
  sectionName,
}: SectionSearchInputProps) {
  const inputRef = useRef<HTMLInputElement>(null)
  const {currentUser} = useUser()
  const handleBlur = useCallback(() => !isDirty && onClose(), [isDirty, onClose])

  useEffect(() => {
    if (isOpen) {
      requestAnimationFrame(() => inputRef.current?.focus())
    }
  }, [isOpen])

  return (
    <Box sx={{ml: 2, display: 'flex', minWidth: 0, '> *': {minWidth: 0, maxWidth: 400}}}>
      {isOpen ? (
        <SearchInput
          userAvatarUrl={currentUser?.avatarUrl}
          userLogin={currentUser?.login}
          id={`search-query-input-${sectionName}`}
          inputRef={inputRef}
          ariaLabel={LABELS.sectionSearchInputAriaLabel}
          placeholder={LABELS.sectionSearchInputPlaceholder}
          searchQuery={searchQuery}
          onBlur={handleBlur}
          onSearch={onSearch}
          onSetSearchQuery={onSetSearchQuery}
        />
      ) : (
        // eslint-disable-next-line primer-react/a11y-remove-disable-tooltip
        <IconButton
          unsafeDisableTooltip={true}
          aria-label={`Open ${sectionName} search query input`}
          icon={SearchIcon}
          sx={{color: 'fg.muted'}}
          variant="invisible"
          onClick={onOpen}
        />
      )}
    </Box>
  )
}

export interface SectionHeaderProps extends Pick<SectionSearchInputProps, 'onSearch'> {
  activeSearchQuery: string
  canDelete?: boolean
  count?: number
  initialSearchQuery: string
  isOperationRunning?: boolean
  onOperationFinish?: () => void
  onOperationStart?: () => void
  savedViewId?: string
  title: string
}

export default function SectionHeader({
  activeSearchQuery,
  canDelete,
  count,
  initialSearchQuery,
  isOperationRunning,
  onOperationFinish,
  onOperationStart,
  savedViewId,
  title,
  ...searchInputProps
}: SectionHeaderProps) {
  const {addToast} = useToastContext()
  const environment = useRelayEnvironment()
  const [isEditingTitle, setIsEditingTitle] = useState(false)
  const [isDeleteDialogOpen, setIsDeleteDialogOpen] = useState(false)
  const [sectionTitle, setSectionTitle] = useState(title)
  const [searchQueryInputValue, setSearchQueryInputValue] = useState(activeSearchQuery)
  const [isSearchInputOpen, setIsSearchInputOpen] = useState(false)
  const navigate = useNavigate()
  const withinListView = useIsWithinListView()

  const exploreQueryUrl = searchUrl(activeSearchQuery)
  const isViewPersisted = !!savedViewId

  const handleOpenInput = useCallback(() => setIsSearchInputOpen(true), [])
  const handleCloseInput = useCallback(() => setIsSearchInputOpen(false), [])

  const handleDelete = () => {
    if (!isViewPersisted) return

    onOperationStart?.()
    const onError = () => {
      // eslint-disable-next-line @github-ui/dotcom-primer/toast-migration
      addToast({
        type: 'error',
        message: `Failed to delete ${title} section.`,
      })
      onOperationFinish?.()
    }

    const onCompleted = () => {
      // eslint-disable-next-line @github-ui/dotcom-primer/toast-migration
      addToast({
        type: 'success',
        message: `Successfully deleted ${title} section.`,
      })
      onOperationFinish?.()
    }

    deleteSavedViewMutation({
      environment,
      input: {
        savedViewId,
      },
      onCompleted,
      onError,
    })
  }

  const handleSave = useCallback(() => {
    if (!isViewPersisted) return

    onOperationStart?.()
    const onError = () => {
      // eslint-disable-next-line @github-ui/dotcom-primer/toast-migration
      addToast({
        type: 'error',
        message: `Failed to save ${title} section.`,
      })
      onOperationFinish?.()
    }

    const onCompleted = () => {
      // eslint-disable-next-line @github-ui/dotcom-primer/toast-migration
      addToast({
        type: 'success',
        message: `Successfully saved ${title} section.`,
      })
      onOperationFinish?.()
      setIsEditingTitle(false)
      handleCloseInput()
    }

    updateSavedViewMutation({
      environment,
      input: {
        savedViewId,
        name: sectionTitle,
        query: searchQueryInputValue,
      },
      onCompleted,
      onError,
    })
  }, [
    addToast,
    environment,
    handleCloseInput,
    isViewPersisted,
    onOperationFinish,
    onOperationStart,
    savedViewId,
    searchQueryInputValue,
    sectionTitle,
    title,
  ])

  const handleCancelEdit = useCallback(() => {
    setIsEditingTitle(false)
    setSectionTitle(title)
  }, [title])

  const onSectionFilterLinkClick = useCallback(
    (e: React.MouseEvent<HTMLAnchorElement, MouseEvent>) => {
      e.preventDefault()
      navigate(exploreQueryUrl, {state: {skipTurbo: true}})
    },
    [navigate, exploreQueryUrl],
  )

  const sectionFilterLink = useMemo(
    () => (
      <ListViewSectionFilterLink
        title={title}
        count={count}
        href={exploreQueryUrl}
        onClick={onSectionFilterLinkClick}
      />
    ),
    [exploreQueryUrl, title, onSectionFilterLinkClick, count],
  )

  const titleSection = useMemo(() => {
    if (isEditingTitle) {
      return (
        <Box
          sx={{
            display: 'flex',
            flexDirection: 'row',
            flexGrow: 1,
            alignItems: 'center',
            gap: 2,
          }}
        >
          <TextInput
            value={sectionTitle}
            sx={{fontSize: 1, fontWeight: 'normal', lineHeight: '20px'}}
            onChange={e => setSectionTitle(e.target.value)}
          />
          <Button onClick={handleCancelEdit}>Cancel</Button>
          <Button variant="primary" onClick={handleSave}>
            Save
          </Button>
        </Box>
      )
    }
  }, [isEditingTitle, handleCancelEdit, handleSave, sectionTitle])

  const contents = (
    <>
      {isOperationRunning && <Spinner size="small" />}
      <SectionSearchInput
        isDirty={initialSearchQuery !== searchQueryInputValue}
        isOpen={isSearchInputOpen}
        searchQuery={searchQueryInputValue}
        sectionName={title}
        onClose={handleCloseInput}
        onOpen={handleOpenInput}
        onSetSearchQuery={setSearchQueryInputValue}
        {...searchInputProps}
      />
      {isViewPersisted && (
        <SectionActionMenu
          anchorSx={{ml: 1}}
          buttonAriaLabel={`More options for section ${title}`}
          canDelete={canDelete}
          onDelete={() => setIsDeleteDialogOpen(true)}
          onEditTitle={() => setIsEditingTitle(true)}
          onSaveChanges={handleSave}
          isSectionDirty={sectionTitle !== title || initialSearchQuery !== searchQueryInputValue}
        />
      )}
      {isDeleteDialogOpen && (
        <DeleteSectionDialog onClose={() => setIsDeleteDialogOpen(false)} onDelete={handleDelete} title={title} />
      )}
    </>
  )

  if (!withinListView) {
    return (
      <Box
        sx={{
          pl: 3,
          pr: 2,
          py: 2,
          backgroundColor: 'canvas.subtle',
          borderTopRightRadius: 2,
          borderTopLeftRadius: 2,
          borderBottom: '1px solid',
          borderBottomColor: 'border.muted',
          display: 'flex',
          justifyContent: 'flex-start',
          alignItems: 'center',
        }}
      >
        <Box sx={{flex: 1}} role="region" aria-label={sectionTitle} aria-live="polite" aria-atomic="true">
          {titleSection ?? sectionFilterLink}
        </Box>
        {contents}
      </Box>
    )
  }

  return (
    <ListViewMetadata title={titleSection} sectionFilters={isEditingTitle ? undefined : [sectionFilterLink]}>
      {contents}
    </ListViewMetadata>
  )
}

try{ SectionSearchInput.displayName ||= 'SectionSearchInput' } catch {}
try{ SectionHeader.displayName ||= 'SectionHeader' } catch {}