// eslint-disable-next-line no-restricted-imports
import {useToastContext} from '@github-ui/toast/ToastContext'
import {useUser} from '@github-ui/use-user'
import {PlusIcon} from '@primer/octicons-react'
import {Box, Button, FormControl, TextInput} from '@primer/react'
import {useRef, useState} from 'react'
import {useRelayEnvironment} from 'react-relay'

import {createSavedViewMutation} from '../../mutations/create-saved-view-mutation'
import SearchInput from '../SearchInput'

function AddSectionForm({
  isSaving,
  onCancel,
  onSave,
}: {
  isSaving?: boolean
  onCancel?: () => void
  onSave?: (title: string, query: string) => void
}) {
  const [title, setTitle] = useState('')
  const [query, setQuery] = useState('')
  const inputRef = useRef<HTMLInputElement>(null)
  const {currentUser} = useUser()

  return (
    <Box
      sx={{
        alignItems: 'flex-end',
        backgroundColor: 'canvas.subtle',
        border: '1px solid',
        borderColor: 'border.default',
        borderRadius: 2,
        display: 'flex',
        flexDirection: 'row',
        gap: 2,
        px: 3,
        py: 2,
        // give some margin on the bottom so the query builder has room to expand
        mb: '300px',
      }}
    >
      <Box sx={{display: 'flex', flexDirection: 'column', flexBasis: '33%'}}>
        <FormControl>
          <FormControl.Label>Title</FormControl.Label>
          <TextInput
            placeholder="Enter a title"
            sx={{width: '100%'}}
            value={title}
            onChange={e => setTitle(e.target.value)}
          />
        </FormControl>
      </Box>
      <Box sx={{display: 'flex', flexDirection: 'column', flexBasis: '67%'}}>
        <FormControl>
          <FormControl.Label>Query</FormControl.Label>
          <Box sx={{width: '100%', '> *': {width: '100%'}}}>
            <SearchInput
              ariaLabel="New section query builder"
              id="new-section-query-builder"
              inputRef={inputRef}
              placeholder="Enter a query"
              searchQuery={query}
              sxString="display:block; width:100%"
              userAvatarUrl={currentUser?.avatarUrl}
              userLogin={currentUser?.login}
              onSetSearchQuery={setQuery}
            />
          </Box>
        </FormControl>
      </Box>
      <Button disabled={isSaving} onClick={onCancel}>
        Cancel
      </Button>
      <Button disabled={isSaving} variant="primary" onClick={() => onSave?.(title, query)}>
        Save
      </Button>
    </Box>
  )
}

export default function AddSection({
  collectionId,
  savedViewsConnectionId,
}: {
  collectionId: string
  savedViewsConnectionId: string
}) {
  const [isActive, setIsActive] = useState(false)
  const [isSaving, setIsSaving] = useState(false)
  const environment = useRelayEnvironment()
  const {addToast} = useToastContext()

  const handleSave = (title: string, query: string) => {
    setIsSaving(true)
    const onError = () => {
      setIsSaving(false)
      // eslint-disable-next-line @github-ui/dotcom-primer/toast-migration
      addToast({
        type: 'error',
        message: `Failed to create ${title} section`,
      })
    }

    const onCompleted = () => {
      setIsSaving(false)
      setIsActive(false)
    }

    createSavedViewMutation({
      connectionId: savedViewsConnectionId,
      environment,
      input: {
        collectionId,
        name: title,
        query,
      },
      onCompleted,
      onError,
    })
  }

  return isActive ? (
    <AddSectionForm isSaving={isSaving} onCancel={() => setIsActive(false)} onSave={handleSave} />
  ) : (
    // give some bottom margin so the form isn't off the screen when activated
    <Box sx={{mb: 3}}>
      <Button leadingVisual={PlusIcon} onClick={() => setIsActive(true)}>
        Add new section
      </Button>
    </Box>
  )
}

try{ AddSectionForm.displayName ||= 'AddSectionForm' } catch {}
try{ AddSection.displayName ||= 'AddSection' } catch {}