import {commitMutation, graphql} from 'react-relay'
import type {Environment} from 'relay-runtime'

import {VALUES} from '../constants'
import type {
  createReviewsCollectionMutation,
  createReviewsCollectionMutation$data,
  CreateSavedCollectionInput,
} from './__generated__/createReviewsCollectionMutation.graphql'

type createReviewsCollectionMutationProps = {
  environment: Environment
  input: CreateSavedCollectionInput
  onError?: (error: Error) => void
  onCompleted?: (response: createReviewsCollectionMutation$data) => void
  viewerId: string
}

export function createReviewsCollectionMutation({
  environment,
  input,
  onError,
  onCompleted,
  viewerId,
}: createReviewsCollectionMutationProps) {
  return commitMutation<createReviewsCollectionMutation>(environment, {
    mutation: graphql`
      mutation createReviewsCollectionMutation($input: CreateSavedCollectionInput!, $maxSavedViews: Int!)
      @raw_response_type {
        createSavedCollection(input: $input) {
          dashboard {
            reviewsCollection {
              id
              color
              description
              icon
              name
              savedViews(first: $maxSavedViews, orderBy: {field: CREATED_AT, direction: ASC}) {
                edges {
                  node {
                    id
                    name
                    query
                  }
                }
              }
            }
          }
        }
      }
    `,
    variables: {
      input,
      maxSavedViews: VALUES.maxSavedViews,
    },
    updater(store) {
      const payloadRoot = store.getRootField('createSavedCollection')
      const updatedDashboard = payloadRoot.getLinkedRecord('dashboard')

      if (viewerId) {
        const record = store.get(viewerId)
        record?.setLinkedRecord(updatedDashboard, 'dashboard')
      }
    },
    onError: error => onError?.(error),
    onCompleted: (response: createReviewsCollectionMutation$data) => onCompleted?.(response),
  })
}
