import {TransitionType} from '@github-ui/react-core/app-routing-types'
import {jsonRoute} from '@github-ui/react-core/json-route'
import {registerReactAppFactory} from '@github-ui/react-core/register-app'

import {App} from './App'
import {Layout} from './pages/Layout'
import {AssignedRedirect, PullRequestsPageContent} from './pages/PullRequestsPage'

registerReactAppFactory('pulls-dashboard', () => ({
  App,
  routes: [
    jsonRoute({
      path: '/pulls',
      Component: Layout,
      transitionType: TransitionType.TRANSITION_WITHOUT_FETCH,
      children: [
        {path: '/pulls', Component: PullRequestsPageContent},
        {path: '/pulls/assigned', Component: AssignedRedirect},
        {path: '/pulls/mentioned', Component: PullRequestsPageContent},
        {path: '/pulls/review-requested', Component: PullRequestsPageContent},
      ],
    }),
  ],
}))
