import {relayEnvironmentWithMissingFieldHandlerForNode} from '@github-ui/relay-environment'
import {Box} from '@primer/react'
import type {ReactNode} from 'react'
import {memo, Suspense} from 'react'
import type {Environment} from 'react-relay'
import {RelayEnvironmentProvider} from 'react-relay'

import {QueryContextProvider} from './contexts'

export const App = memo(function App({children, environment}: {children?: ReactNode; environment?: Environment}) {
  return (
    <RelayEnvironmentProvider environment={environment ?? relayEnvironmentWithMissingFieldHandlerForNode()}>
      <QueryContextProvider>
        <Suspense fallback={null}>
          <Box sx={{bg: 'canvas.default', height: '100%', display: 'flex', flexDirection: 'column'}}>{children}</Box>
        </Suspense>
      </QueryContextProvider>
    </RelayEnvironmentProvider>
  )
})

try{ App.displayName ||= 'App' } catch {}