import {Box} from '@primer/react'
import {Suspense, useEffect} from 'react'
import {graphql, useLazyLoadQuery, useRelayEnvironment} from 'react-relay'

import AddSectionForm from '../components/collection/AddSectionForm'
import SavedView from '../components/collection/SavedView'
import Section from '../components/collection/Section'
import {SectionLoading} from '../components/collection/SectionLoading'
import {REVIEWS_COLLECTION, VALUES} from '../constants'
import {createReviewsCollectionMutation} from '../mutations/create-reviews-collection-mutation'
import type {ReviewsQuery} from './__generated__/ReviewsQuery.graphql'

let collectionSeedAttempted = false

function ReviewsFallbackSections() {
  const defaultViews = REVIEWS_COLLECTION.savedViews
  return (
    <Suspense
      fallback={
        <>
          {defaultViews.map((defaultView, index) => (
            <SectionLoading
              key={index}
              title={defaultView.name}
              activeSearchQuery={defaultView.query}
              initialSearchQuery={defaultView.query}
            />
          ))}
        </>
      }
    >
      {defaultViews.map((defaultView, index) => (
        <Section key={index} name={defaultView.name} initialSearchQuery={defaultView.query} />
      ))}
    </Suspense>
  )
}

function ReviewsWithQuery() {
  const viewerData = useLazyLoadQuery<ReviewsQuery>(
    graphql`
      query ReviewsQuery($maxSavedViews: Int!) {
        viewer {
          id
          dashboard {
            id
            reviewsCollection {
              id
              savedViews(first: $maxSavedViews, orderBy: {field: CREATED_AT, direction: ASC}) {
                __id
                edges {
                  node {
                    ...SavedView_savedView
                    id
                    name
                    query
                  }
                }
              }
            }
          }
        }
      }
    `,
    {maxSavedViews: VALUES.maxSavedViews},
  )

  const environment = useRelayEnvironment()

  const reviewsCollection = viewerData.viewer.dashboard?.reviewsCollection
  const hasReviewsCollection = !!reviewsCollection
  const canDelete = reviewsCollection?.savedViews.edges?.length !== 1

  useEffect(() => {
    // seed the user's reviews collection if they don't have one
    if (collectionSeedAttempted || hasReviewsCollection) return

    collectionSeedAttempted = true

    createReviewsCollectionMutation({
      environment,
      input: {
        protectedType: 'REVIEWS',
        ...REVIEWS_COLLECTION,
      },
      viewerId: viewerData.viewer.id,
    })
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [])

  return (
    <Box
      data-testid="reviewsPage"
      sx={{
        display: 'flex',
        flexDirection: 'column',
        gap: 3,
      }}
    >
      {hasReviewsCollection ? (
        <Suspense
          fallback={reviewsCollection.savedViews.edges?.map(
            (savedView, index) =>
              !!savedView?.node && (
                <SectionLoading
                  key={index}
                  activeSearchQuery={savedView.node.query}
                  canDelete={canDelete}
                  initialSearchQuery={savedView.node.query}
                  savedViewId={savedView.node.id}
                  title={savedView.node.name}
                />
              ),
          )}
        >
          {reviewsCollection.savedViews.edges?.map(
            (savedView, index) =>
              !!savedView?.node && <SavedView key={index} canDelete={canDelete} savedView={savedView.node} />,
          )}
          <AddSectionForm
            collectionId={reviewsCollection.id}
            savedViewsConnectionId={reviewsCollection.savedViews.__id}
          />
        </Suspense>
      ) : (
        <ReviewsFallbackSections />
      )}
    </Box>
  )
}

export function Reviews() {
  return (
    <Suspense fallback={<></>}>
      <ReviewsWithQuery />
    </Suspense>
  )
}

try{ ReviewsFallbackSections.displayName ||= 'ReviewsFallbackSections' } catch {}
try{ ReviewsWithQuery.displayName ||= 'ReviewsWithQuery' } catch {}
try{ Reviews.displayName ||= 'Reviews' } catch {}