import {commitMutation, graphql} from 'react-relay'
import type {Environment} from 'relay-runtime'

import type {
  CreateSavedViewInput,
  createSavedViewMutation,
  createSavedViewMutation$data,
} from './__generated__/createSavedViewMutation.graphql'

type CreateSavedViewMutationProps = {
  connectionId: string
  environment: Environment
  input: CreateSavedViewInput
  onError?: (error: Error) => void
  onCompleted?: (response: createSavedViewMutation$data) => void
}

export function createSavedViewMutation({
  connectionId,
  environment,
  input,
  onError,
  onCompleted,
}: CreateSavedViewMutationProps) {
  return commitMutation<createSavedViewMutation>(environment, {
    mutation: graphql`
      mutation createSavedViewMutation($connections: [ID!]!, $input: CreateSavedViewInput!) @raw_response_type {
        createSavedView(input: $input) {
          savedView @appendNode(connections: $connections, edgeTypeName: "SavedViewEdge") {
            id
            query
            name
          }
        }
      }
    `,
    variables: {
      input,
      connections: [connectionId],
    },
    onError: error => onError?.(error),
    onCompleted: (response: createSavedViewMutation$data) => onCompleted?.(response),
  })
}
