import {HOTKEYS} from '@github-ui/issue-viewer/Hotkeys'
import {
  createFilter,
  createFilterWithRepoScope,
  createGraphQlFilter,
  createUserFilter,
} from '@github-ui/list-view-items-issues-prs/Query'
import {
  FILTERS,
  KEY_ONLY_FILTERS,
  STATIC_VALUE_FILTERS,
  USER_VALUE_FILTERS,
} from '@github-ui/query-builder/constants/search-filters'
import {IssuesFilterProvider} from '@github-ui/query-builder/providers/issues-filter-provider'
import {LabelFilterProvider} from '@github-ui/query-builder/providers/label-filter-provider'
import {OrgFilterProvider} from '@github-ui/query-builder/providers/org-filter-provider'
import {ProjectFilterProvider} from '@github-ui/query-builder/providers/project-filter-provider'
import {QueryBuilder} from '@github-ui/query-builder/QueryBuilder'
import type {QueryBuilderElement} from '@github-ui/query-builder-element'
import {useKeyPress} from '@github-ui/use-key-press'
import {useUser} from '@github-ui/use-user'
import {Box} from '@primer/react'
import type {KeyboardEventHandler} from 'react'
import type React from 'react'
import {useCallback, useEffect, useRef, useTransition} from 'react'
import {useRelayEnvironment} from 'react-relay'
import {useLocation} from 'react-router-dom'

import {RepoFilterProvider} from '../../../react-shared/query-builder/providers/RepoFilterProvider'
import {LABELS, TEST_IDS} from '../../constants'
import {useQueryContext} from '../../contexts'
import {searchUrl} from '../../utils/urls'

export function SearchBar() {
  const ref = useRef<HTMLInputElement>(null)
  const {dirtySearchQuery, setActiveSearchQuery, setDirtySearchQuery, setIsQueryLoading, executeQuery} =
    useQueryContext()
  const {search} = useLocation()
  const relayEnvironment = useRelayEnvironment()

  const urlSearchParams = new URLSearchParams(search)
  const urlQuery = urlSearchParams.get('q')

  const [, startTransition] = useTransition()

  useEffect(() => {
    setActiveSearchQuery(urlQuery || '')
    setDirtySearchQuery(urlQuery || '')
  }, [setActiveSearchQuery, setDirtySearchQuery, urlQuery])

  const handleEnterKeyPress: KeyboardEventHandler = useCallback(
    e => {
      // eslint-disable-next-line @github-ui/ui-commands/no-manual-shortcut-logic -- manual shortcut logic is idiomatic in React
      if (e.key === 'Enter' && dirtySearchQuery && executeQuery) {
        setIsQueryLoading(true)
        startTransition(() => {
          executeQuery(dirtySearchQuery)
        })
      }
    },
    [dirtySearchQuery, executeQuery, setIsQueryLoading],
  )

  const onShortcutKeyPress = useCallback((e: KeyboardEvent) => {
    // eslint-disable-next-line @github-ui/ui-commands/no-manual-shortcut-logic
    if (e.altKey && e.shiftKey && ref && ref.current) {
      ref.current.focus()
      e.preventDefault()
    }
  }, [])

  const onInputChange: React.ChangeEventHandler<HTMLInputElement> = useCallback(
    e => {
      setDirtySearchQuery(e.currentTarget.value)
      if (e.currentTarget.value !== dirtySearchQuery) {
        const url = searchUrl(e.currentTarget.value)
        window.history.replaceState(history.state, '', url)
      }
    },
    [setDirtySearchQuery, dirtySearchQuery],
  )

  useKeyPress([HOTKEYS.focusSearch], onShortcutKeyPress)

  const {currentUser} = useUser()

  const onRequestProvider = useCallback(
    (event: Event) => {
      event.stopPropagation()
      const queryBuilder = event.target as QueryBuilderElement

      // The providers are registering themselves on the query builder.
      KEY_ONLY_FILTERS.map(f => createFilter(IssuesFilterProvider, f, queryBuilder))
      STATIC_VALUE_FILTERS.map(f => createFilter(IssuesFilterProvider, f, queryBuilder))
      USER_VALUE_FILTERS.map(f => createUserFilter(f, queryBuilder, currentUser?.login ?? '', currentUser?.avatarUrl))
      // We don't want to suggest cross-repo bounds if we're already scoped to a repo
      createGraphQlFilter(OrgFilterProvider, {...FILTERS.org, relayEnvironment}, queryBuilder)
      createGraphQlFilter(RepoFilterProvider, {...FILTERS.repo, relayEnvironment}, queryBuilder)

      createFilterWithRepoScope(LabelFilterProvider, FILTERS.label, queryBuilder)
      createFilterWithRepoScope(ProjectFilterProvider, FILTERS.project, queryBuilder)
    },
    [currentUser?.avatarUrl, currentUser?.login, relayEnvironment],
  )

  return (
    <Box
      sx={{
        width: '100%',
        px: 0,
        display: 'block',
        flexDirection: 'row',
        flexWrap: 'wrap',
        justifyContent: 'space-between',
        gap: 2,
      }}
    >
      <Box sx={{display: 'flex', flexDirection: 'row', gap: 2, flex: 1, minWidth: 0}}>
        <QueryBuilder
          id="search"
          inputRef={ref}
          label={LABELS.issueSearchInputAriaLabel}
          data-testid={TEST_IDS.searchInput}
          placeholder={LABELS.issueSearchInputPlaceholder}
          inputValue={dirtySearchQuery}
          onKeyPress={handleEnterKeyPress}
          onChange={onInputChange}
          sxString="display:block; width:100%"
          onRequestProvider={onRequestProvider}
        />
      </Box>
    </Box>
  )
}

try{ SearchBar.displayName ||= 'SearchBar' } catch {}