import {Dialog} from '@primer/react/drafts'

export default function DeleteSectionDialog({
  onClose,
  onDelete,
  title,
}: {
  onClose: () => void
  onDelete: () => void
  title: string
}) {
  return (
    <Dialog
      aria-label={`Delete section ${title}?`}
      title="Delete section?"
      footerButtons={[
        {content: 'Cancel', onClick: onClose, buttonType: 'normal'},
        {content: 'Delete', onClick: onDelete, buttonType: 'danger'},
      ]}
      onClose={onClose}
      width="small"
    >
      Are you sure you want to delete the section {title}?
    </Dialog>
  )
}

try{ DeleteSectionDialog.displayName ||= 'DeleteSectionDialog' } catch {}