import {eventToHotkeyString} from '@github-ui/hotkey'
import {createFilter, createGraphQlFilter, createUserFilter} from '@github-ui/list-view-items-issues-prs/Query'
import {
  FILTERS,
  KEY_ONLY_FILTERS,
  STATIC_VALUE_FILTERS,
  TEAM_VALUE_FILTERS,
  USER_VALUE_FILTERS,
} from '@github-ui/query-builder/constants/search-filters'
import {IssuesFilterProvider} from '@github-ui/query-builder/providers/issues-filter-provider'
import {LabelFilterProvider} from '@github-ui/query-builder/providers/label-filter-provider'
import {OrgFilterProvider} from '@github-ui/query-builder/providers/org-filter-provider'
import {ProjectFilterProvider} from '@github-ui/query-builder/providers/project-filter-provider'
import {TeamFilterProvider} from '@github-ui/query-builder/providers/team-filter-provider'
import {QueryBuilder} from '@github-ui/query-builder/QueryBuilder'
import type {QueryBuilderElement} from '@github-ui/query-builder-element'
import type {KeyboardEvent, RefObject} from 'react'
import {useCallback, useEffect} from 'react'
import {useRelayEnvironment} from 'react-relay'

import {RepoFilterProvider} from '../../react-shared/query-builder/providers/RepoFilterProvider'

interface SearchInputProps {
  ariaLabel: string
  id: string
  inputRef: RefObject<HTMLInputElement>
  onBlur?: (event: FocusEvent) => void
  onSearch?: (query: string) => void
  onSetSearchQuery: (query: string) => void
  placeholder: string
  searchQuery: string
  sxString?: string
  userLogin?: string
  userAvatarUrl?: string
}

export default function SearchInput({
  ariaLabel,
  id,
  inputRef,
  onBlur,
  onSearch,
  onSetSearchQuery,
  placeholder,
  searchQuery,
  sxString,
  userLogin,
  userAvatarUrl,
}: SearchInputProps) {
  const handleInputKeyDown = (e: KeyboardEvent<HTMLElement>) => {
    if (eventToHotkeyString(e.nativeEvent) === 'Enter') {
      onSearch?.(searchQuery)
    }
  }
  const relayEnvironment = useRelayEnvironment()

  const onRequestProvider = useCallback(
    (event: Event) => {
      event.stopPropagation()
      const queryBuilder = event.target as QueryBuilderElement

      // The providers are registering themselves on the query builder.
      KEY_ONLY_FILTERS.map(f => createFilter(IssuesFilterProvider, f, queryBuilder))
      STATIC_VALUE_FILTERS.map(f => createFilter(IssuesFilterProvider, f, queryBuilder))
      USER_VALUE_FILTERS.map(f => createUserFilter(f, queryBuilder, userLogin ?? '', userAvatarUrl ?? ''))
      TEAM_VALUE_FILTERS.map(f => createFilter(TeamFilterProvider, f, queryBuilder))
      createGraphQlFilter(OrgFilterProvider, {...FILTERS.org, relayEnvironment}, queryBuilder)
      createFilter(LabelFilterProvider, FILTERS.label, queryBuilder)
      createGraphQlFilter(RepoFilterProvider, {...FILTERS.repo, relayEnvironment}, queryBuilder)
      createFilter(ProjectFilterProvider, FILTERS.project, queryBuilder)
    },
    [relayEnvironment, userAvatarUrl, userLogin],
  )

  useEffect(() => {
    if (!onBlur) return

    // QueryBuilder doesn't accept onBlur handler, so we're doing it the old fashioned way
    const input = inputRef.current
    input?.addEventListener('blur', onBlur)
    return () => input?.removeEventListener('blur', onBlur)
  }, [inputRef, onBlur])

  return (
    <QueryBuilder
      label={ariaLabel}
      id={id}
      inputRef={inputRef}
      inputValue={searchQuery}
      placeholder={placeholder}
      sxString={sxString}
      onKeyPress={handleInputKeyDown}
      onChange={e => onSetSearchQuery(e.currentTarget.value)}
      onRequestProvider={onRequestProvider}
    />
  )
}

try{ SearchInput.displayName ||= 'SearchInput' } catch {}