import {noop} from '@github-ui/noop'
import type {ReactNode} from 'react'
import {createContext, useContext, useMemo, useState} from 'react'
import {useLocation} from 'react-router-dom'

type QueryContextType = {
  activeSearchQuery: string
  dirtySearchQuery: string
  executeQuery: ((query: string) => void) | undefined
  isQueryLoading: boolean
  setActiveSearchQuery: (query: string) => void
  setDirtySearchQuery: (query: string) => void
  setExecuteQuery: (executeQuery: (query: string) => void) => void
  setIsQueryLoading: (loading: boolean) => void
}

type QueryContextProviderType = {
  children: ReactNode
}

const QueryContext = createContext<QueryContextType>({
  isQueryLoading: false,
  setIsQueryLoading: noop,
  activeSearchQuery: '',
  setActiveSearchQuery: noop,
  dirtySearchQuery: '',
  setDirtySearchQuery: noop,
  executeQuery: undefined,
  setExecuteQuery: noop,
})

export function QueryContextProvider({children}: QueryContextProviderType) {
  const {search} = useLocation()
  const urlSearchParams = new URLSearchParams(search)
  const urlQuery = urlSearchParams.get('q') ?? ''

  const [activeSearchQuery, setActiveSearchQuery] = useState<string>(urlQuery)
  const [dirtySearchQuery, setDirtySearchQuery] = useState<string>(urlQuery)
  const [isQueryLoading, setIsQueryLoading] = useState<boolean>(false)
  const [executeQuery, setExecuteQuery] = useState<((query: string) => void) | undefined>()

  const queryContextValue = useMemo<QueryContextType>(() => {
    return {
      isQueryLoading,
      setIsQueryLoading,
      activeSearchQuery,
      setActiveSearchQuery,
      dirtySearchQuery,
      setDirtySearchQuery,
      executeQuery,
      setExecuteQuery,
    }
  }, [isQueryLoading, activeSearchQuery, dirtySearchQuery, executeQuery])

  return <QueryContext.Provider value={queryContextValue}>{children}</QueryContext.Provider>
}

export function useQueryContext() {
  return useContext(QueryContext)
}

try{ QueryContext.displayName ||= 'QueryContext' } catch {}
try{ QueryContextProvider.displayName ||= 'QueryContextProvider' } catch {}